import React, { useEffect, useState } from 'react';

const KaKaoMap = () => {
  const [position, setPosition] = useState();
  const [lat, setLat] = useState();
  const [lng, setLng] = useState();

  useEffect(() => {
    // 현재 위치 가져오기
    navigator.geolocation.getCurrentPosition(
      (position) => {
        const { latitude, longitude } = position.coords;
        setPosition({ lat: latitude, lng: longitude });
        setLat(latitude);
        setLng(longitude);
      },
      (error) => {
        console.error('Error getting current position:', error.message);
      }
    );

    // 위치 업데이트 주기 설정 (예: 1초마다)
    const watchPositionOptions = { enableHighAccuracy: true, timeout: 1000, maximumAge: 0 };
    const watchPositionId = navigator.geolocation.watchPosition(
      (position) => {
        const { latitude, longitude } = position.coords;
        setPosition({ lat: latitude, lng: longitude });
        // 지도 중심 업데이트
        map.setCenter(new window.kakao.maps.LatLng(latitude, longitude));
      },
      (error) => {
        console.error('Error watching position:', error.message);
      },
      watchPositionOptions
    );

  
    // 카카오 지도 렌더링
    const container = document.getElementById('map');
    const options = {
      center: new window.kakao.maps.LatLng(lat, lng), // 서울의 위도, 경도
      level: 1, // 지도 확대 레벨
    };

    const map = new window.kakao.maps.Map(container, options);
    var markerPosition  = new window.kakao.maps.LatLng(lat, lng); 
    var marker = new window.kakao.maps.Marker({
      position: markerPosition
    });
    marker.setMap(map);

    // 컴포넌트 언마운트 시 위치 감시 중단
    return () => {
      navigator.geolocation.clearWatch(watchPositionId);
    };
  }, []);

  return (
    <div id="map" style={{ width: '100%', height: '680px' }}>
      
    </div>
    
  );
};

export default KaKaoMap;